
/*********************** This is box-one ***********/

 .flex_box{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
 }


.main_head{
    color: #0A0E0F;
    font-weight: 700;
    font-size: 24px;
    line-height: 24px;
    letter-spacing: 0.15px;
    font-family: 'Nunito Sans', sans-serif;
    padding: 15px;

}

.box_one{
    width:254px;
    /* height: 450px; */ 
    background-color: #fff;
    box-shadow: 0px 1px 8px rgba(0, 0, 0, 0.08);
    /* margin: 7px; */
    border-radius: 16px;
    
}
.box_one h1 {
    color: #0A0E0F;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.15px;
    font-family: 'Nunito', sans-serif;
} 
.box_one h2{
    font-size: 24px;
    color:#0A0E0F;
    font-family: 'Nunito Sans', sans-serif;
    letter-spacing: 0.18px; 
    line-height: 24px;
    /* gap: 40px; */
    padding-top: 24px;
    padding-left: 24px;
    font-weight: 700;

}
.box_one h3{
    font-weight: 700;
    font-size: 24px;
    line-height: 24px;
    color: #4E4F51;
    padding: 0px 24px;
    font-family: 'Nunito Sans', sans-serif;
}
.b1-btn_one{
    background: #433F3F;
    box-shadow: inset 1px 1px 0px rgba(255, 255, 255, 0.25), inset -1px -1px 0px rgba(0, 0, 0, 0.25);
    border-radius: 24px;
    font-weight: 800;
    font-size: 15px;
    line-height: 24px;
    text-align: center;
    color: #fff;
    padding: 10px 60px;
    margin: 20px;
    font-family: 'Nunito Sans', sans-serif;
    border: none;
    /* width: 220px;
    height: 40px; */
}


