}
.list_item li::before {
    content: url(img/Vector-right.png);
    font-size: 20px;
    vertical-align: middle;
    padding-right: 10px;
    
}
.list_item li{
    list-style: none;
    padding: 7px;
}
.list_item{
   padding-left: 20px;
}

 
